


























import uniIcon from '../uni-icon/uni-icon.vue'
export default {
	name: "uni-pagination",
	components: {
		uniIcon
	},
	props: {
		prevText: {
			type: String,
			default: '上一页'
		},
		nextText: {
			type: String,
			default: '下一页'
		},
		current: {
			type: [Number, String],
			default: 1
		},
		total: { //数据总量
			type: [Number, String],
			default: 0
		},
		pageSize: { //每页数据量
			type: [Number, String],
			default: 10
		},
		showIcon: { //是否以 icon 形式展示按钮
			type: [Boolean, String],
			default: false
		}
	},
	watch: {
		current(val) {
			this.currentIndex = Number(val)
		}
	},
	data() {
		return {
			currentIndex: Number(this.current)
		}
	},
	computed: {
		maxPage() {
			let maxPage = 1
			let total = Number(this.total)
			let pageSize = Number(this.pageSize)
			if (total && pageSize) {
				maxPage = Math.ceil(total / pageSize)
			}
			return maxPage
		},
		setLeftBtnClass() {
			let classList = ['uni-pagination__btn']
			if (Number(this.currentIndex) === 1) {
				classList.push('uni-pagination--disabled')
			}
			return classList
		},
		setLeftBtnHoverClass() {
			let hoverClass = 'uni-pagination--hover'
			if (Number(this.currentIndex) === 1) {
				hoverClass = ''
			}
			return hoverClass
		},
		setRightBtnClass() {
			let classList = ['uni-pagination__btn']
			if (Number(this.currentIndex) === this.maxPage) {
				classList.push('uni-pagination--disabled')
			}
			return classList
		},
		setRightBtnHoverClass() {
			let hoverClass = 'uni-pagination--hover'
			if (Number(this.currentIndex) === this.maxPage) {
				hoverClass = ''
			}
			return hoverClass
		}
	},
	methods: {
		clickLeft() {
			if (Number(this.currentIndex) === 1) {
				return
			}
			this.currentIndex -= 1
			this.change('prev')
		},
		clickRight() {
			if (Number(this.currentIndex) === this.maxPage) {
				return
			}
			this.currentIndex += 1
			this.change('next')
		},
		change(e) {
			this.$emit('change', {
				type: e,
				current: this.currentIndex
			})
		}
	}
}
